import { getAppInfo } from "zmp-sdk/apis"


window.getAppInfo = function getZaloApplication() {
    console.log("getZaloApplication ===========");

    getAppInfo({
        success: (data) => {
            // x l khi gi api thnh cng
            const { name, version } = data;
        },
        fail: (error) => {
            // x l khi gi api tht bi
            console.log(error);
        }
    });
}

